import React, {Component} from 'react';
import ImageGallery from 'react-image-gallery';
import "react-image-gallery/styles/css/image-gallery.css";
import './App.css';

class App extends Component {
    constructor() {
        super(Component);
        this.state = {
            containerStyles: this.generateContainerStyles(),
            images: Array.from(Array(9).keys())
                .map(function (x) {
                    return {
                        original: "./assets/mihalykoles/g_koles_mihaly_0000" + (x + 1) + ".jpeg",
                        thumbnail: "./assets/mihalykoles/g_koles_mihaly_0000" + (x + 1) + ".jpeg"
                    }
                })
        };
        this.updateStyle = this.updateStyle.bind(this);
    }

    componentDidMount() {
        // Additionally I could have just used an arrow function for the binding `this` to the component...
        window.addEventListener("resize", this.updateStyle);
    }

    calculateDimensions() {
        let newWidth = window.innerHeight*1.57;
        if (newWidth > window.innerWidth) {
            newWidth = window.innerWidth;
        }

        return {
            height: window.innerHeight,
            width: newWidth,
        };
    }

    generateContainerStyles() {
        let sizes = this.calculateDimensions();
        return {
            width: `${sizes.width}px`,
            height: `${sizes.height}px`,
            margin: 'auto'
        };
    }

    updateStyle() {
        this.setState({
            containerStyles: this.generateContainerStyles()
        });
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.updateStyle);
    }

    render() {
        return (
            <div style={this.state.containerStyles}>
                <ImageGallery items={this.state.images} slideInterval={10000}/>
            </div>
        );
    }
}

export default App;
